html,body {
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.my-drawer {
  top: 45px;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.my-drawer .am-drawer-sidebar {
  background-color: #fff;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.my-drawer .am-drawer-sidebar .am-list {
  width: 300px;
  padding: 0;
}
.am-list-content a {
  display: block;
  color: #333333;
}
.am-image-picker {
  width: 60%;
  margin: 30% auto 0;
}
.am-image-picker-list .am-flexbox-align-center > div.am-flexbox-item:not(:first-child){
  display:none;
}
.idx-imgbox {
  text-align: center;
  font-size: 18px;
}
.c-about-list .am-list-extra {
  -webkit-flex-basis: 200px !important;
          flex-basis: 200px !important;
}
.am-list-body > div {
  border-bottom: solid 1px #efefef !important;
}
.fate-result {
  font-size: 18px;
  text-align: center;  
}
/**/
.am-navbar-right > span {
  margin-left: 8px;
}
.img-con {
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.img-con img {
  width: 100%;
}
.img-text {
  text-align: center;
  margin: 8px 0;  
}
.img-zoombox, .pinch-zoom-container{
  height: 100vw !important;
}

