.my-drawer {
  top: 45px;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.my-drawer .am-drawer-sidebar {
  background-color: #fff;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.my-drawer .am-drawer-sidebar .am-list {
  width: 300px;
  padding: 0;
}
.am-list-content a {
  display: block;
  color: #333333;
}
.am-image-picker {
  width: 60%;
  margin: 30% auto 0;
}
.am-image-picker-list .am-flexbox-align-center > div.am-flexbox-item:not(:first-child){
  display:none;
}
.idx-imgbox {
  text-align: center;
  font-size: 18px;
}